import Vue from 'vue'
import Router from 'vue-router'
import Controller from '@/pages/controller/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component:Controller //resolve=>require(['@/pages/controller/index'],resolve)
    }
    
  ]
})



