<template>
    <div class="articleList">
        
        <div style="padding:2vw;font-size:0.35rem;">
            <div id="mescroll" class="mescroll">
                <ul id="newsList" class="new-list" v-infinite-scroll="loadMore"
                    infinite-scroll-disabled="loading"
                    infinite-scroll-distance="10">

                <li class="AListBox">
                    <p style="color:red;font-weight:600;margin:1vw;">Data Summary</p>
                    <div style="height:15vw;color:#6D6D6D;">
                        <div>
                            <div style="width:50%;height:8vw;float:left;text-align:center;line-height:8vw;">
                                <p style="float:right;height:8vw;border-right:1px solid #cdcdcd;"></p>
                                Total User: {{TotalUser | NumberFormat}}
                                
                            </div>
                            <div style="width:50%;height:10vw;float:right;text-align:center;line-height:8vw;">
                                Total Buzz: {{TotalPosts | NumberFormat}}
                            </div>
                        </div>
                        <div class="AListFooter" style="display:flex;padding:1vw;text-align:center;margin-top:2vw;color:#999999;width:100%;">
                            <div class="AF_Icon"><img :src="TransmitIcon" alt=""><span> {{TotalTransmit | NumberFormat}}</span></div>
                            <div class="AF_Icon"><img :src="CommentIcon" alt=""><span> {{TotalComment | NumberFormat}}</span></div>
                            <div class="AF_Icon"><img :src="LikeIcon" alt=""><span> {{TotalLike | NumberFormat}}</span></div>
                            <div class="AF_Icon"><img :src="MergeIcon" alt=""><span> {{TotalMerge | NumberFormat}}</span></div>
                        </div>
                    </div>
                </li>
                <li class="AListBox" :key="index" v-for="(item,index) in DataList">
                    <a target="_blank" :href="item.tweetURL">
                    <div class="AListHeader">
                        <img :src="item.portrait" alt="">
                        <span class="AListNick">{{item.username}}</span>
                        <span class="AListDate">&nbsp;&nbsp;{{item.commentTime}}</span>
                    </div>
                    <div class="AListContent">
                        <p>{{item.commentContent}}</p>
                    </div>
                    </a>
                    <div class="AListFooter" style="display:flex;padding:1vw;text-align:center;margin-top:2vw;color:#999999;">
                        <div class="AF_Icon"><img :src="TransmitIcon" alt=""><span> {{item.retweet | NumberFormat}}</span></div>
                        <div class="AF_Icon"><img :src="CommentIcon" alt=""><span> {{item.comment | NumberFormat}}</span></div>
                        <div class="AF_Icon"><img :src="LikeIcon" alt=""><span> {{item.like | NumberFormat}}</span></div>
                        <div class="AF_Icon"><img :src="MergeIcon" alt=""><span> {{item.sum | NumberFormat}}</span></div>
                    </div>
                </li>
                    <li style="text-align:center;" v-show="loading">
                        <cube-loading :size="28" style="margin:auto;text-align:center;width:28px;"></cube-loading>
                    </li>
                    <li style="text-align:center;" v-if="bottomShow">
                        <p style="margin-top:3px;"></p>
                    </li>
                </ul>


            </div>
        </div>
      
        <div style="display:flex;width:100%;font-size:0.3rem;position:fixed;z-index:99;bottom:0px;background:#4C4C4C;height:12vw;color:#ffffff;padding-top:1.5vw;">
            <div class="footer-btn" @click="selectDate" :style="`color:${footerArrColor[0].Color}`"><img :src="footerIconDate"><br><span>{{footerDateText}}</span></div>
            <div class="footer-btn" @click="selectTranmit" :style="`color:${footerArrColor[2].Color}`"><img :src="footerIconTransmit"><br><span>{{footerTransmitText}}</span></div>
            
            <div class="footer-btn" @click="selectComment" :style="`color:${footerArrColor[1].Color}`"><img :src="footerIconComment"><br><span>{{footerCommentText}}</span></div>
            <div class="footer-btn" @click="selectLike" :style="`color:${footerArrColor[3].Color}`"><img :src="footerIconLike"><br><span>{{footerLikeText}}</span></div>
            <div class="footer-btn" @click="selectMerge" :style="`color:${footerArrColor[4].Color}`"><img :src="footerIconMerge"><br><span>{{footerMergeText}}</span></div>
        </div>
    </div>
</template>
<script>

import Commenti from '@/images/comment.png';
import Like from '@/images/like.png';
import Merge from '@/images/merge.png';
import Transmit from '@/images/transmit.png';

import Flike from '@/images/fLike.png';
import Fmerge from '@/images/fMerge.png';
import Ftransmit from '@/images/fTransmit.png';
import Fcomment from '@/images/fComment.png';
import Fdate from '@/images/fDate.png';

import AFlike from '@/images/AFlike.png';
import AFcomment from '@/images/AFcomment.png';
import AFdate from '@/images/AFdate.png';
import AFmerge from '@/images/AFmerge.png';
import AFtransmit from '@/images/AFtransmit.png';



import { InfiniteScroll } from 'mint-ui';

import {getTweetList} from '@/api/index'
export default {
    name:'articleList',
    components:{
        InfiniteScroll
    },
    props:{
        QueryType:{
            type:Array
        },
        startTime:{
            required:true,
            type:String
        },
        endTime:{
            required:true,
            type:String
        }
    },
    data(){
        return{
            k:10,
            bottomShow:false,
            DataList:[],
            loading:false,
            footerArrColor:[{Color:'red'},{Color:'#ffffff'},{Color:'#ffffff'},{Color:'#ffffff'},{Color:'#ffffff'}],
            sortWord:'commentTime',
            sortOrder:'asc',
            footerDateText:'L-H',
            footerCommentText:'H-L',
            footerTransmitText:'H-L',
            footerLikeText:'H-L',
            footerMergeText:'H-L',
            pageing:{
                limit:10,
                page:1
            },

            CommentIcon:Commenti,
            LikeIcon:Like,
            MergeIcon:Merge,
            TransmitIcon:Transmit,

            FlikeIcon:Flike,
            FmergeIcon:Fmerge,
            FtransmitIcon:Ftransmit,
            FcommentIcon:Fcomment,
            FdateIcon:Fdate,

            AFlikeIcon:AFlike,
            AFcommentIcon:AFcomment,
            AFdateIcon:AFdate,
            AFmergeIcon:AFmerge,
            AFtransmitIcon:AFtransmit,

            footerIconDate:AFdate,
            footerIconComment:Fcomment,
            footerIconTransmit:Ftransmit,
            footerIconLike:Flike,
            footerIconMerge:Fmerge,

            MeScrollDom:null,

            totalPages:'0',

            TotalUser:'',
            TotalPosts:'',
            TotalTransmit:'',
            TotalComment:'',
            TotalLike:'',
            TotalMerge:''
        }
    },
    filters:{
        NumberFormat(value){
            return value.toString().replace(/(\d{1,3})(?=(\d{3})+$)/g,'$1,');
        }
    },
    methods:{
        getTweetListQuery(){
            getTweetList(this.startTime,this.endTime,this.sortWord,this.sortOrder,this.QueryType,this.pageing.page).then(Response=>{
                if(Response.data.result==1){
                    // this.DataList.push.apply(this.DataList,Response.data.data.list)
                    this.DataList=Response.data.data.list
                    this.totalPages=Response.data.data.totalPages;
                    this.TotalUser=Response.data.data.userCount;
                    this.TotalPosts=Response.data.data.totalEl;
                    this.pageing.page+=1;
                    this.TotalMerge=Response.data.data.sumCount;
                    this.TotalLike=Response.data.data.likeCount;
                    this.TotalTransmit=Response.data.data.retweetCount;
                    this.TotalComment=Response.data.data.commentCount;
                    try{
                        if(Response.data.data.list.length==0){
                            console.log(1)
                            this.bottomShow=true;
                        }else{
                            console.log(2)
                            this.bottomShow=false;
                        }
                    }catch(e){
                        console.log(3)
                    }
                    
                }else{
                    this.$createToast({
                        type: 'error',
                        txt: '',
                        time: 1000
                    }).show()
                }
                this.loading=false
                
            }).catch(e=>{
                this.loading=false
                this.buttomShow=false;
            })
        },
        loadMore(){
                this.loading = true;
                console.log(this.pageing.page,this.totalPages)
                if(this.pageing.page>this.totalPages){
                    this.loading=false;
                    this.bottomShow=true;
                
                }else{
                    this.getTweetListQuery();
                }
        },
        selectDate(e){
            this.pageing.page=1;
            this.DataList=[];
            this.sortWord='commentTime'
            if(this.footerArrColor[0].Color=='red'){
                if(this.footerDateText=='L-H'){//
                    this.footerDateText='H-L'
                    this.sortOrder='desc';
                }else{
                    this.footerDateText='L-H';
                    this.sortOrder='asc'
                }
            }else{
                if(this.footerDateText=='L-H'){
                    this.sortOrder='asc'
                }else{
                    this.sortOrder='desc';
                }
                this.footerArrColor[0].Color='red';
                this.footerArrColor[1].Color='#ffffff';
                this.footerArrColor[2].Color='#ffffff';
                this.footerArrColor[3].Color='#ffffff';
                this.footerArrColor[4].Color='#ffffff';
                this.footerIconDate=AFdate;
                this.footerIconComment=Fcomment;
                this.footerIconTransmit=Ftransmit;
                this.footerIconMerge=Fmerge;
                this.footerIconLike=Flike;
            }
            this.getTweetListQuery();
            
            
        },
        selectComment(){
            this.DataList=[];
            this.pageing.page=1;
            this.sortWord='comment';
            if(this.footerArrColor[1].Color=='red'){
                if(this.footerCommentText=='H-L'){
                    this.footerCommentText='L-H'
                    
                    this.sortOrder='asc'
                }else{
                    this.footerCommentText='H-L'
                    this.sortOrder='desc'
                }
            }else{
                if(this.footerCommentText=='H-L'){
                    this.sortOrder='desc'
                }else{
                    this.sortOrder='asc'
                }
                this.footerArrColor[0].Color='#ffffff';
                this.footerArrColor[1].Color='red';
                this.footerArrColor[2].Color='#ffffff';
                this.footerArrColor[3].Color='#ffffff';
                this.footerArrColor[4].Color='#ffffff';
                this.footerIconDate=Fdate;
                this.footerIconComment=AFcomment;
                this.footerIconTransmit=Ftransmit;
                this.footerIconMerge=Fmerge;
                this.footerIconLike=Flike;
            }
            this.getTweetListQuery();

        },
        selectTranmit(){
            this.DataList=[];
            this.pageing.page=1;
            this.sortWord='retweet';
            if(this.footerArrColor[2].Color=='red'){
                if(this.footerTransmitText=='H-L'){
                    this.footerTransmitText='L-H'
                    this.sortOrder='asc';
                }else{
                    this.footerTransmitText='H-L'
                    this.sortOrder='desc';
                }
            }else{
                if(this.footerTransmitText=='H-L'){
                    this.sortOrder='desc';
                }else{
                    this.sortOrder='asc';
                }
                this.footerArrColor[0].Color='#ffffff';
                this.footerArrColor[1].Color='#ffffff';
                this.footerArrColor[2].Color='red';
                this.footerArrColor[3].Color='#ffffff';
                this.footerArrColor[4].Color='#ffffff';
                this.footerIconDate=Fdate;
                this.footerIconComment=Fcomment;
                this.footerIconTransmit=AFtransmit;
                this.footerIconMerge=Fmerge;
                this.footerIconLike=Flike;
            }
            this.getTweetListQuery();
        },
        selectLike(){
            this.DataList=[];
            this.pageing.page=1;
            this.sortWord='like'
            if(this.footerArrColor[3].Color=='red'){
                if(this.footerLikeText=='H-L'){
                    this.footerLikeText='L-H'
                    this.sortOrder='asc';
                }else{
                    this.footerLikeText='H-L'
                    this.sortOrder='desc';
                }
            }else{
                if(this.footerLikeText=='H-L'){
                    this.sortOrder='desc';
                }else{
                    this.sortOrder='asc';
                }
                this.footerArrColor[0].Color='#ffffff';
                this.footerArrColor[1].Color='#ffffff';
                this.footerArrColor[2].Color='#ffffff';
                this.footerArrColor[3].Color='red';
                this.footerArrColor[4].Color='#ffffff';
                this.footerIconDate=Fdate;
                this.footerIconComment=Fcomment;
                this.footerIconTransmit=Ftransmit;
                this.footerIconMerge=Fmerge;
                this.footerIconLike=AFlike;
            }
            this.getTweetListQuery();
        },
        selectMerge(){
            this.DataList=[]
            this.pageing.page=1;
            this.sortWord='sum'
            if(this.footerArrColor[4].Color=='red'){
                if(this.footerMergeText=='H-L'){
                    this.footerMergeText='L-H'
                    this.sortOrder='asc';
                }else{
                    this.footerMergeText='H-L'
                    this.sortOrder='desc';
                }
            }else{
                if(this.footerMergeText=='H-L'){
                    this.sortOrder='desc';
                }else{
                    this.sortOrder='asc';
                }
                this.footerArrColor[0].Color='#ffffff';
                this.footerArrColor[1].Color='#ffffff';
                this.footerArrColor[2].Color='#ffffff';
                this.footerArrColor[3].Color='#ffffff';
                this.footerArrColor[4].Color='red';
                this.footerIconDate=Fdate;
                this.footerIconComment=Fcomment;
                this.footerIconTransmit=Ftransmit;
                this.footerIconMerge=AFmerge;
                this.footerIconLike=Flike;
            }
            this.getTweetListQuery();
        }
    },
    beforeCreate(){
        this.Zloading=true
    },
    created(){
        this.getTweetListQuery();
    },
    mounted(){
    
    },
    watch:{
        QueryType(n,o){
            console.log(n,o)
            this.pageing.page=1
            this.DataList=[]
            this.getTweetListQuery()
        },
        startTime(n,o){
            console.log(n,o)
        },
        endTime(n,o){
            console.log(n,o)
            this.pageing.page='1';
            this.DataList=[]
            this.getTweetListQuery()
        }
    }
}
</script>
<style scoped>
.articleList{
    margin-bottom: 12.5vw;
    /* padding:1vw; */
    text-align: left;
    background: #F1F1F1;
    padding-bottom:3vw;
}
.footer-btn{
    width:20vw;
    text-align: center;
}
.footer-btn span{
    position: relative;
    top:1.5vw;
}
.footer-btn img{
    width:5vw;
    height: 5vw;
}
.AListBox{
    background:#ffffff;
    padding: 1vw;
    margin-top:2vw;
    /* margin-bottom:1vw; */
    border-radius: 5px;
}
.AListHeader{
    vertical-align: middle;
}
.AListHeader img{
    width:10vw;
    height:10vw;
    border-radius: 100%;
    vertical-align: middle;
    display: inline-block;
}
.AListNick{
    display: inline-block;
    max-width:33vw;
    margin-left: 3vw;
    color: #AAAAAA;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
    position: relative;
    top: 1px;
}
.AListDate{
    color: #CDCDCD;
    
}
.AListContent{
    padding:0.5vw;
    line-height: 0.5rem;
    color: #383838;
}
.AListContent p{
    overflow: hidden;
    text-overflow: ellipsis;
    display: box;
    display: -webkit-box;
    line-clamp: 3;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    letter-spacing: 0px;
}
.AF_Icon{
    width:25%;
}
.AF_Icon img{
    width:3vw;
    height:3vw;
    position: relative;
    top: 1.5px;
}
</style>



