<template>
    <div class="ChartMap">
        <div style="background:#ffffff;padding-top:1vw;border-radius:5px;">
            <div style="display:flex;width:96%;padding:2%;text-align:center;">
                <div class="selectStatus"><span :class="changeClass[0].class" @click="mainWordCloud">Posts</span><br><span v-show="changeClass[0].class=='selectTrue'" style="width:0.7rem;background:#ED4A4A;display:inline-block;height:2px;"></span></div>
                <div class="selectStatus"><span :class="changeClass[1].class" @click="commentWordCloud">Comments</span><br><span v-show="changeClass[1].class=='selectTrue'" style="width:0.7rem;background:#ED4A4A;display:inline-block;height:2px;"></span></div>
            </div>
            <div class="wordcloud">
                <div style="height:100%;" id="wordCloudDom">

                </div>
            </div>
            <div class="lineChart">
                <div style="height:100%;" id="lineChartDom">

                </div>
            </div>
        </div>
        <div v-if="false" style="background:#ffffff;margin-top:3vw;border-radius:5px;">
            <div class="competingChart">
                <p style="padding-top:3vw;font-size:0.5rem;padding-left:3vw;color:#D2002F;"></p>
                <div style="height:90vw;" id="competingChartDom">

                </div>
            </div>
        </div>
        <div style="background:#ffffff;margin-top:3vw;border-radius:5px;">
            <div class="footballChart">
                <p style="padding-top:3vw;font-size:0.5rem;padding-left:3vw;color:#D2002F;border-bottom:1px solid #f1f1fa;padding-bottom:5px;">Team Buzz</p>
                <div style="height:90vw;" id="FootballTeam">

                </div>
            </div>
        </div>
        <div style="background:#ffffff;margin-top:3vw;border-radius:5px;">
            <div class="footballChart">
                <p style="padding-top:3vw;font-size:0.5rem;padding-left:3vw;color:#D2002F;border-bottom:1px solid #f1f1fa;padding-bottom:5px;">Player Buzz</p>
                <div style="height:90vw;" id="FootballStars">

                </div>
            </div>
        </div>
        <div style="background:#ffffff;margin-top:3vw;border-radius:5px;">
            <div class="Sponsor">
                <p style="padding-top:3vw;font-size:0.5rem;padding-left:3vw;color:#D2002F;border-bottom:1px solid #f1f1fa;padding-bottom:5px;">Sponsor Brand SOV</p>
                <div style="padding:2vw 1vw 2vw;">
                    <table style="width:100%;text-align:center;">
                        <tr style="height:0.6rem;">
                            <th style="font-weight:600;width:25%;">Rank</th>
                            <th style="font-weight:600;width:25%;">Brand</th>
                            <th style="font-weight:600;width:25%;">BUZZ</th>
                            <th style="font-weight:600;width:25%;">%</th>
                        </tr>
                        <tr :key="sIndex" v-for="(sItem,sIndex) in sponsorList" style="color:#9A9A9A;">
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{sIndex+1}}</span>
                                </div>
                            </td>
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{sItem.name}}</span>
                                </div>
                            </td>
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{sItem.count | NumberFormat}}</span>
                                </div>
                            </td>
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{sItem.prop}}</span>
                                </div>
                            </td>
                        </tr>
                        <tr style="border-top:1px solid #ececec;padding-top:0.2rem;line-height:0.8rem;">
                            <!-- <td colspan="2" style="font-weight:600;text-align:right;color:#D2002F;">Total:</td> -->
                            <td></td>
                            <td style="font-weight:600;color:#D2002F;">Total</td>
                            <td style="font-weight:600;">{{sponsorTotalBuzz | NumberFormat}}</td>
                            <td style="font-weight:600;">{{sponsorTotalPercent | NumberFormat}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
        <div style="background:#ffffff;margin-top:3vw;border-radius:5px;">
            <div class="offical">
                <p style="padding-top:3vw;font-size:0.5rem;padding-left:3vw;color:#D2002F;border-bottom:1px solid #f1f1fa;padding-bottom:5px;">Non-offical Brand SOV</p>
                <div style="padding:2vw 1vw 2vw;">
                    <table style="width:100%;text-align:center;">
                        <tr style="height:0.6rem;">
                            <th style="font-weight:600;width:25%;">Rank</th>
                            <th style="font-weight:600;width:25%;">Brand</th>
                            <th style="font-weight:600;width:25%;">BUZZ</th>
                            <th style="font-weight:600;width:25%;">%</th>
                        </tr>
                        <tr :key="nIndex" v-for="(nItem,nIndex) in nonOfficalList" style="color:#9A9A9A;">
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{nIndex+1}}</span>
                                </div>
                            </td>
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{nItem.name}}</span>
                                </div>
                            </td>
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{nItem.count | NumberFormat}}</span>
                                </div>
                            </td>
                            <td style="height:0.7rem;line-height:0.7rem;">
                                <div style="width:20vw;">
                                    <span>{{nItem.prop}}</span>
                                </div>
                            </td>
                        </tr>
                        <tr style="border-top:1px solid #ececec;padding-top:0.2rem;line-height:0.8rem;">
                            <!-- <td colspan="2" style="font-weight:600;text-align:right;color:#D2002F;">Total:</td> -->
                            <td></td>
                            <td style="font-weight:600;color:#D2002F;">Total</td>
                            <td style="font-weight:600;">{{nonOfficalTotalBuzz | NumberFormat}}</td>
                            <td style="font-weight:600;">{{nonOfficalTotalPercent | NumberFormat}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
        <div style="background:#ffffff;margin-top:3vw;border-radius:5px;">
            <div class="top10Weibo">
                <p style="padding-top:3vw;font-size:0.5rem;padding-left:3vw;color:#D2002F;border-bottom:1px solid #f1f1fa;padding-bottom:5px;">Top {{ArticleUserTotal}} Active Users on Weibo</p>
                <div style="padding:1vw 1vw 2vw;" id="FootballStars">
                    <table style="width:100%;text-align:center;">
                        <tr style="line-height:0.8rem;">
                            <th style="font-weight:600;"> 
                                Profile Photo

                            </th>
                            <th><strong style="font-weight:600;">User Name</strong></th>
                            <th @click="handlePostsSort"><Strong style="font-weight:600;">Posts  </Strong><img :src="PostsImage" alt="" :style="`width:0.503rem;height:0.426rem;position:relative;top:0.05rem;opacity:${PostsOpacity};`"></th>
                            <th @click="handleCommentSort"><strong style="font-weight:600;">Comment</strong><img :src="CommentImage" alt="" :style="`width:0.503rem;height:0.426rem;position:relative;top:0.05rem;opacity:${CommentOpacity}`"></th>
                        </tr>
                        <tr :key="index" v-for="(item,index) in ArticleUser" style="line-height:0.6rem;color:#9A9A9A;">
                            <td><img style="width:5vw;height:5vw;border-radius:100%;" :src="item.portrait" alt=""></td>
                            <td><a :href="item.userURL" target="_blank"><span style="width:35vw;overflow:hidden;text-overflow:ellipsis;white-space: nowrap;display:inline-block;">{{item.username}}</span></a></td>
                            <td>{{item.tweetNum | NumberFormat}}</td>
                            <td>{{item.commentNum | NumberFormat}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>

    </div>
</template>
<script>

import echarts from 'echarts';
import 'echarts-wordcloud';
import { getTweetWordFrequency ,getCommentWordFrequency ,getLabelRank ,getActiveUser ,getBrandRank} from '@/api/index'
const SortUp='data:image/png;base64,'
const SortDown='data:image/png;base64,'
export default {
    name:'chartMap',
    components:{

    },
    props:{
        QueryType:{
            type:Array
        },
        startTime:{
            required:true,
            type:String
        },
        endTime:{
            required:true,
            type:String
        }
    },
    filters:{
        NumberFormat(value){
            return value.toString().replace(/(\d{1,3})(?=(\d{3})+$)/g,'$1,');
        }
    },
    data(){
        return{
            PostsOpacity:'1',
            CommentOpacity:'0',
            
            PostsImage:SortDown,
            CommentImage:SortDown,
            Zloading:false,
            changeClass:[{class:'selectTrue'},{class:'selectFalse'}],
            wordcloudArr:[],
            wordcloudTweetArr:[],
            wordcloudCommentArr:[],
            teamDataX:[],
            teamDataY:[],
            playerDataX:[],
            playerDataY:[],
            ArticleUser:[],
            lineChart:[{name:'',Arr:[]}],
            tweetLineChart:{time:[]},
            commentLineChart:{time:[]},
            sponsorList:[],
            sponsorTotalBuzz:'',
            sponsorTotalPercent:'',
            nonOfficalList:[],
            nonOfficalTotalBuzz:'',
            nonOfficalTotalPercent:'',
            ArticleUserTotal:'0',
            ArticleSortOrder:'desc',
            ArticleSortWord:'Posts'
        }
    },
    methods:{
        handlePostsSort(){
            if(this.PostsOpacity=='1'){
                if(this.PostsImage==SortUp){
                    this.PostsImage=SortDown
                    this.ArticleSortOrder='desc'
                }else{
                    this.PostsImage=SortUp
                    this.ArticleSortOrder='asc'
                }
            }else{
                this.PostsImage=SortDown
                this.ArticleSortOrder='desc'
            }
            this.CommentOpacity='0';
            this.PostsOpacity='1';
            this.ArticleSortWord='Posts'
            this.getActiveUserQuery()
        },
        handleCommentSort(){
            if(this.CommentOpacity=='1'){
                if(this.CommentImage==SortUp){
                    this.CommentImage=SortDown
                    this.ArticleSortOrder='desc'
                }else{
                    this.CommentImage=SortUp
                    this.ArticleSortOrder='asc'
                }
            }else{
                this.CommentImage=SortDown
                this.ArticleSortOrder='desc'
            }
            this.CommentOpacity='1';
            this.PostsOpacity='0';
            this.ArticleSortWord='Comment'
            this.getActiveUserQuery()
        },
        DataInit(){
            this.getTweetWordFrequencyQuery();
            this.getCommentWordFrequencyQuery();
            this.getLabelRankQuery();
            this.getActiveUserQuery();
            this.getBrandRankQuery();
        },
        getBrandRankQuery(){
            getBrandRank(this.startTime,this.endTime,this.QueryType).then(Response=>{
                console.log("zheshishdsfkjsfksjflksjaflsjfalksa")
                if(Response.data.result==1){
                    this.sponsorList=Response.data.data.sponsor.list;
                    this.sponsorTotalBuzz=Response.data.data.sponsor.sum;
                    this.sponsorTotalPercent=Response.data.data.sponsor.totalProp

                    this.nonOfficalList=Response.data.data.nonSponsor.list;
                    this.nonOfficalTotalBuzz=Response.data.data.nonSponsor.sum;
                    this.nonOfficalTotalPercent=Response.data.data.nonSponsor.totalProp
                    console.log(this.sponsorList,this.nonOfficalList,'fuck')
                }
            })
        },
        getTweetWordFrequencyQuery(){
            getTweetWordFrequency(this.startTime,this.endTime,this.QueryType).then(Response=>{
                if(Response.data.result==1){
                    this.wordcloudTweetArr=Response.data.data.tweet;
                    this.tweetLineChart.time=Response.data.data.trend.time;
                    let words=Response.data.data.trend.words;
                    let Arr=[]
                    let aaaArr=words[0];
                    for(let bbb in aaaArr){
                        Arr.push({name:bbb,arr:[]})
                    }
                    // for(let i of words){
                    //     console.log(i)
                    //     for(let key in i){
                    //         console.log(key,i[key])
                    //     }
                    // }
                    for(let p=0;p<words.length;p++){
                        for(let k in words[p]){
                            for(let ccc=0;ccc<Arr.length;ccc++){
                                if(k==Arr[ccc].name){
                                    let www=words[p]
                                    Arr[ccc].arr.push(www[k])
                                }
                            }
                        }
                    }
                    this.tweetLineChart.Arr=Arr

                    console.log(Arr,this.tweetLineChart)

                    this.mainWordCloud();
                }
            })
        },
        getCommentWordFrequencyQuery(){
            getCommentWordFrequency(this.startTime,this.endTime,this.QueryType).then(Response=>{
                if(Response.data.result==1){
                    this.wordcloudCommentArr=Response.data.data.comment;
                    this.commentLineChart.time=Response.data.data.trend.time;
                    let words=Response.data.data.trend.words;
                    let Arr=[]
                    let aaaArr=words[0];
                    for(let bbb in aaaArr){
                        Arr.push({name:bbb,arr:[]})
                    }
                    for(let p=0;p<words.length;p++){
                        for(let k in words[p]){
                            for(let ccc=0;ccc<Arr.length;ccc++){
                                if(k==Arr[ccc].name){
                                    let www=words[p]
                                    Arr[ccc].arr.push(www[k])
                                }
                            }
                        }
                    }
                    this.commentLineChart.Arr=Arr
                }
            })
        },
        getLabelRankQuery(){
            getLabelRank(this.startTime,this.endTime,this.QueryType).then(Response=>{
                if(Response.data.result==1){
                    let teamData=Response.data.data.teamData;
                    let playerData=Response.data.data.playerData;
                    let tX=[],tY=[],pX=[],pY=[];
                    for(let Tkey in teamData){
                        tX.push(teamData[Tkey]);
                        tY.push(Tkey);
                    };
                    this.teamDataX=tX//.reverse();
                    this.teamDataY=tY//.reverse();
                    this.footballTeamInit();
                    console.log('123',this.teamDataY,this.teamDataX)
                    for(let Pkey in playerData){
                        pX.push( playerData[Pkey]);
                        pY.push(Pkey);
                    }
                    this.playerDataX=pX.reverse();
                    this.playerDataY=pY.reverse();
                    this.footballStarsInit();
                    console.log('123',this.playerDataY,this.playerDataX)
                }
            })
        },
        getActiveUserQuery(){
            getActiveUser(this.startTime,this.endTime,this.QueryType,this.ArticleSortOrder,this.ArticleSortWord).then(Response=>{
                if(Response.data.result==1){
                    this.ArticleUser=Response.data.data.list;
                    this.ArticleUserTotal=Response.data.data.total;
                }
            })
        },
        mainWordCloud(){
            this.wordcloudArr=this.wordcloudTweetArr;
            this.lineChart=this.tweetLineChart;
            this.changeClass[0].class="selectTrue"
            this.changeClass[1].class="selectFalse"
            this.wordcloudInit();
            this.lineChartInit();
        },
        commentWordCloud(){
            this.wordcloudArr=this.wordcloudCommentArr;
            this.lineChart=this.commentLineChart;
            this.changeClass[0].class="selectFalse"
            this.changeClass[1].class="selectTrue"
            this.wordcloudInit();
            this.lineChartInit();
        },
        wordcloudInit(){
            let options={

                backgroundColor: '#ffffff',
                tooltip: {
                    show: true
                },
                series: [{
                    name: '',
                    type: 'wordCloud',
                    size: ['15%', '50%'],
                    sizeRange: [13, 30],
                    textRotation: [0, 45, 90, -45],
                    rotationRange: [-0, 0],
                    shape: 'circle',
                    textPadding: 0,
                    autoSize: {
                        enable: true,
                        minSize: 6
                    },
                    textStyle: {
                        normal: {
                            color: function() {
                                return 'rgb(' + [
                                    Math.round(Math.random() * 160),
                                    Math.round(Math.random() * 160),
                                    Math.round(Math.random() * 160)
                                ].join(',') + ')';
                            }
                        }
                    
                    },
                    data: this.wordcloudArr
                }]
            }
            let wordcloudInitDOM=echarts.init(document.querySelector(`#wordCloudDom`))
            wordcloudInitDOM.setOption(options)
        },
        lineChartInit(){
                    let abc=[]
                    let abc1=[]
                    console.log(this.lineChart.Arr)
                    try{
                        for(let ol=0;ol<this.lineChart.Arr.length;ol++){
                            abc.push(
                                {
                                    name:this.lineChart.Arr[ol].name,
                                    data: this.lineChart.Arr[ol].arr,
                                    type: 'line',
                                    smooth: true
                                }
                            )
                            abc1.push(this.lineChart.Arr[ol].name)
                        }
                    }catch(e){
                        console.log(e)
                    }
                    
                    console.log(abc)
            
            let options={
                xAxis: {
                    type: 'category',
                    data: this.lineChart.time
                },
                tooltip : {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        label: {
                            backgroundColor: '#6a7985'
                        }
                    }
                },
                 dataZoom: [{
        type: 'inside',
        start: 0,
        end: 50
    }, {
        start: 0,
        end: 50,
        handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
        handleSize: '80%',
        
        handleStyle: {
            color: '#fff',
            shadowBlur: 3,
            shadowColor: 'rgba(0, 0, 0, 0.6)',
            shadowOffsetX: 2,
            shadowOffsetY: 2
        },
        showDetail:false,
        textStyle:{
            color:'#fff'
        },
        labelFormatter:null
    }],
                yAxis: {
                    type: 'value',
                    axisLabel:{
                        show:false
                    },
                    axisTick:{
                        show:false
                    },
                    axisLine:{
                        show:false
                    }
                },
                legend:{
                    data:abc1
                },
                series:abc
                //  {
                //     name:'1',
                //     data: [820, 932, 901, 934, 1290, 1330, 1320],
                //     type: 'line',
                //     smooth: true
                // }
            };
            let lineChartInitDom=echarts.init(document.querySelector('#lineChartDom'));
            lineChartInitDom.setOption(options)
        },
        competingChartInit(){
            let options={
                xAxis: {
                    type: 'category',
                    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                },
                tooltip : {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        label: {
                            backgroundColor: '#6a7985'
                        }
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel:{
                        show:false
                    },
                    axisTick:{
                        show:false
                    },
                    axisLine:{
                        show:false
                    }
                },
                legend:{
                    data:['1','2']
                },
                series: [{
                    name:'1',
                    data: [820, 932, 901, 934, 1290, 1330, 1320],
                    type: 'line',
                    smooth: true
                },{
                    name:'2',
                    data: [80, 932, 901, 904, 290, 1130, 1120],
                    type: 'line',
                    smooth: true
                }]
            };
            let competingChartInitDom=echarts.init(document.querySelector('#competingChartDom'));
            competingChartInitDom.setOption(options);
            document.addEventListener('click',()=>{
                competingChartInitDom.dispatchAction({type:'hideTip'})
            })
        },
        footballTeamInit(){
            let _this=this
            let rich={};
            let b=[]
            this.teamDataY.map((v,i)=>{
                console.log(v,i)
                b.push('a'+i)
                rich['a'+i]={
                    height:15,
                    align:'right',
                    backgroundColor:{
                        image:`http://139.198.0.141:82/pic/nationFlag/${v}.png`
                    }
                }
                console.log(rich[v])
            })
            console.log(rich,'123234')
            let options={
                color: ['#3398DB'],
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 
                        type : 'shadow'        // 'line' | 'shadow'
                    },
                    // formatter:'{b}{c}'
                    formatter:params=>{
                        console.log(params)
                        return this.teamDataY[b.indexOf(params[0].name)]+' '+params[0].value.toString().replace(/(\d{1,3})(?=(\d{3})+$)/g,'$1,');
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis : [
                   {
                    type:'value',
                    axisLine:{
                        show:true
                    },
                    splitLine:{
                        show:false
                    }
                   }
                ],
                yAxis : [
                     {
                        type : 'category',
                        data : b,
                        inverse: true,
                        // axisTick: {
                        //     alignWithLabel: true
                        // },
                        stack:'component',
                        axisLabel:{
                            formatter: function (value) {
                                // console.log(value,'fuck')
                                // let a=''
                                // for(let bc=0;bc<b.length;b++){
                                //     console.log(b[bc],value,'zheshi xunhuan')
                                //     if(b[bc]==value){
                                //         console.log(123)
                                //         a=_this.teamDataY[bc]
                                //     }
                                // }
                                return  '{' + value + '| }\n{value|' + _this.teamDataY[b.indexOf(value)] + '}';
                            },
                            margin:1,
                            rich:rich
                        }
                    }
                ],
                series : [
                   {
                        name:'',
                        type:'bar',
                        barWidth: '60%',
                        data:this.teamDataX,
                        itemStyle:{normal: { 
                                 color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                   color:'#F39C1F'
                                }, {
                                    offset: 1,
                                  color:'#F39C1F'
                               }]) 
                            }
                        }                  
                    }
                ]
            }
            let FootballTeamInitDom=echarts.init(document.querySelector('#FootballTeam'))
            FootballTeamInitDom.setOption(options)
        },
        footballStarsInit(){
            let _this=this
            let rich={};
            let c=[]
            this.playerDataY.map((v,i)=>{
                console.log(v)
                c.push('b'+i)
                rich['b'+i]={
                    height:15,
                    align:'right',
                    backgroundColor:{
                        image:`http://139.198.0.141:82/pic/nationFlag/${v.split("_")[1]}.png`
                    }
                }
                console.log(rich)
            })

            let options={
                color: ['#3398DB'],
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 
                        type : 'shadow'        // 'line' | 'shadow'
                    },
                    // formatter:'{b}{c}'
                    formatter:params=>{
                        console.log(params)
                        return this.playerDataY[c.indexOf(params[0].name)]+' '+params[0].value.toString().replace(/(\d{1,3})(?=(\d{3})+$)/g,'$1,');
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis : [
                   {
                       type:'value',
                    //    show:false,
                    axisLine:{
                        show:true
                    },
                    splitLine:{
                        show:false
                    }
                   }
                ],
                yAxis : [
                
                     {
                        type : 'category',
                        data : c,
                        axisTick: {
                            alignWithLabel: true
                        },
                        stack:'component',
                        axisLabel:{
                            formatter: function (value) {
                         
                                return  '{' + value + '| }\n{value|' + _this.playerDataY[c.indexOf(value)] + '}';
                            },
                            margin:1,
                            rich:rich
                        }
                    }
                ],
                series : [
                   {
                        name:'',
                        type:'bar',
                        barWidth: '60%',
                        data:this.playerDataX,
                        itemStyle:{normal: { 
                                 color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                   color:'#F39C1F'
                                }, {
                                    offset: 1,
                                  color:'#F39C1F'
                               }]) 
                            }
                        }               
                    }
                ]
            }
            let FootballStarsInitDom=echarts.init(document.querySelector('#FootballStars'))
            FootballStarsInitDom.setOption(options)
        }
    },
    beforeCreate(){
        this.Zloading=true
    },
    created(){
        this.DataInit();
    },
    mounted(){
        this.wordcloudInit();
        
        this.lineChartInit();
        this.footballTeamInit();
        this.footballStarsInit();
    },
    watch:{
        QueryType(n,o){
            console.log(n,o)
            this.DataInit();
        },
        startTime(n,o){
            console.log(n,o)
        },
        endTime(n,o){
            console.log(n,o)
            this.DataInit();
        }
    }
}
</script>
<style scoped>
.ChartMap{
    font-size:0.35rem;
    background: #f1f1f1;
    padding: 2vw;
    text-align: left;
    overflow: hidden;
    width: 96vw;
}
.selectStatus{
    width:50%;
}
.selectTrue{
    color:#ED4A4A;
}
.wordcloud{
    height:80vw;
}
.lineChart{
    height: 95vw;
}
.competingChart{
    height:100vw;
}
.footballChart{
    height: 100vw;
}
td div{
    overflow:hidden;text-overflow:ellipsis;white-space: nowrap;
}
</style>



