import axios from 'axios';
// axios.defaults.baseURL = process.env.API_ROOT;
// let url='http://139.198.0.141:9999/worldcup/tweet/';
// let url="http://192.168.3.228:8081/worldcup/tweet/";
let url='http://bigdata.colourdata.com.cn/worldcup/tweet/'
axios.defaults.withCredentials=true;
axios.defaults.headers={'Content-Type':'application/x-www-form-urlencoded'}
axios.interceptors.request.use(function (config) {
    /////////////////////////////////////////// 
    // config.headers['X-Token']=getToken()
    return config;
  }, function (error) {
    ///////////////////////////////////////// 
    return Promise.reject(error);
  });

  axios.interceptors.response.use(function (response) {
    //////////////////////////// 

    return response;
  }, function (error) {
    // //////////////////////
    console.log('responseerror:'+error)
    return Promise.reject(error);
  });



  export const getTweetList = (startTime,endTime,sortWord,SortOrder,keyword,page)=>axios({
      url:url+`getTweetList`,
      method:'post',
      //fuck   page
      data:`startTime=${startTime}&endTime=${endTime}&sortWord=${sortWord}&sortOrder=${SortOrder}&pid=${keyword}&page=${1}&size=${20*page}&group=2`
  })
  export const getTweetWordFrequency = (startTime,endTime,keyword)=>axios({
      url:url+'getTweetWordFrequency',
      method:'post',
      data:`startTime=${startTime}&endTime=${endTime}&pid=${keyword}&group=2`
  })
  export const getCommentWordFrequency = (startTime,endTime,keyword)=>axios({
      url:url+'getCommentWordFrequency',
      method:'post',
      data:`startTime=${startTime}&endTime=${endTime}&pid=${keyword}&group=2`
  })
  export const getLabelRank = (startTime,endTime,keyword)=>axios({
      url:url+'getLabelRank ',
      method:'post',
      data:`startTime=${startTime}&endTime=${endTime}&pid=${keyword}&group=2`
  })
  export const getActiveUser = (startTime,endTime,keyword,sortOrder,sortWord)=>axios({
    url:url+'getActiveUser',
    method:'post',
    data:`startTime=${startTime}&endTime=${endTime}&pid=${keyword}&group=2&sortOrder=${sortOrder}&sortWord=${sortWord}`
})
  export const getKeyWords = (startTime,endTime)=>axios({
    url:url+'getKeyWords',
    method:'post',
    data:`startTime=${startTime}&endTime=${endTime}&group=2`
  })

  export const getBrandRank = (startTime,endTime,keyword)=>axios({
    url:url+'getBrandRank',
    method:'post',
    data:`startTime=${startTime}&endTime=${endTime}&pid=${keyword}&group=2`
})


