        </div>
        <ChartMap :QueryType="DataType" :startTime="startTimeFormat" :endTime="endTimeFormat" v-if="handleSelectClass[1].class=='selectStatusT'"/>
        <ArticleList :QueryType="DataType" :startTime="startTimeFormat" :endTime="endTimeFormat"  v-if="handleSelectClass[0].class=='selectStatusT'"/>
