</template>
<script>
import BannerImg from '@/images/Banner.png';
import before1 from '@/images/before1.png'
import before2 from '@/images/before2.png'
import before3 from '@/images/before3.png'
import before4 from '@/images/before4.png'
import active1 from '@/images/active1.png'
import active2 from '@/images/active2.png'
import active3 from '@/images/active3.png'
import active4 from '@/images/active4.png'
// import Swiper from 'swiper';
// import 'swiper/dist/css/swiper.min.css';
import ArticleList from './articleList';
import ChartMap from './chartMap';
import {getKeyWords} from '@/api/index';

export default {
    name:'controller1',
    components:{
        ArticleList,
        ChartMap,
    },
    filters:{
        NumberFormat(value){
            return value.toString().replace(/(\d{1,3})(?=(\d{3})+$)/g,'$1,');
        }
    },
    data(){
        return {
            keyWordsArr:[{"commentNum":'',"term":"","id":1,"posts":''},{"commentNum":'',"term":"","id":2,"posts":''},{"commentNum":'',"term":"","id":3,"posts":''},{"commentNum":'',"term":"","id":4,"posts":''}],
            BannerImgSrc:BannerImg,
            handleImage1:active1,
            handleImage2:before2,
            handleImage3:before3,
            handleImage4:before4,
            startTimeValue:null,
            endTimeValue:null,
            startTimeFormat:'',
            endTimeFormat:'',
            handleSelectClass:[{class:'selectStatusT'},{class:'selectStatusF'}],
            DataType:[2],
            imageType:[1],
            status:0
        }
    },
    created(){
        this.dateInit()
        this.getKeyWordsQuery()
    },
    mounted(){
        // this.swiperInit();
    },
    methods:{
        getKeyWordsQuery(){
            getKeyWords(this.startTimeFormat,this.endTimeFormat).then(Response=>{
                if(Response.data.result==1){
                    this.keyWordsArr=Response.data.data
                    if(this.status==0){
                        this.DataType=[this.keyWordsArr[0].id]
                        this.status=1
                    }
                }
            })
        },
        dateInit(){
            let myDate = new Date();
            let year = myDate.getFullYear();
            let month = myDate.getMonth()+1<10?`0${String(myDate.getMonth()+1)}`:myDate.getMonth()+1;
            let date = myDate.getDate()<10?`0${String(myDate.getDate())}`:myDate.getDate();
            
            console.log(year,month,date)
            this.startTimeFormat=`${year}-${month}-${date} 00:00`;
            this.endTimeFormat=`${year}-${month}-${date} 23:00`
        },
        deleteDate(){
            this.startTimeFormat='';
            this.endTimeFormat='';
        },
        handleActive(v,l){
            console.log(v,l)
            if(this.DataType.indexOf(v)==-1){
                this.DataType.push(v)
            }else{
                if(this.DataType.length>=2){
                    let o=this.DataType.indexOf(v);
                    this.DataType.splice(o,1)
                }
            }
            if(this.imageType.indexOf(l)==-1){
                this.imageType.push(l)
            }else{
                if(this.imageType.length>=2){
                    let o=this.imageType.indexOf(l);
                    this.imageType.splice(o,1)
                }
            }
            console.log(this.imageType)
            // this.DataType=val;
        },
        startTime(){
            this.startTimeValue=this.$createDatePicker({
                title: 'Start',
                format:{
                    year:'YYYY Y',
                    month:'MM M',
                    date:'DD D',
                    hour:'hh H'
                },
                min: new Date(2010, 0, 0 ,0),
                max: new Date(2025, 0, 0,23),
                value: new Date(),
                columnCount:4,
                cancelTxt:'Cancel',
                confirmTxt:'Confirm',
                onSelect: this.selectHandleStart,
                onCancel: this.cancelHandle
            }).show()
            
        },
     
        selectHandleStart(date, selectedVal, selectedText) {
            this.startTimeFormat=`${selectedVal[0]}-${selectedVal[1]<10?'0'+selectedVal[1]:selectedVal[1]}-${selectedVal[2]<10?'0'+selectedVal[2]:selectedVal[2]} ${selectedVal[3]<10?'0'+selectedVal[3]:selectedVal[3]}:00`;
            console.log(selectedVal,selectedText)
            this.endTimeValue=this.$createDatePicker({
                title: 'End',
                format:{
                    year:'YYYY Y',
                    month:'MM M',
                    date:'DD D',
                    hour:'hh H'
                },
                min: new Date(selectedVal[0], selectedVal[1]-1, selectedVal[2],Number(selectedVal[3])+1),
                max: new Date(2025, 0, 0,23),
                value: new Date(),
                columnCount:4,
                cancelTxt:'Cancel',
                confirmTxt:'Confirm',
                onSelect: this.selectHandleEnd,
                onCancel: this.cancelHandle
            }).show()
        },
        selectHandleEnd(date, selectedVal, selectedText){
            this.endTimeFormat=`${selectedVal[0]}-${selectedVal[1]<10?'0'+selectedVal[1]:selectedVal[1]}-${selectedVal[2]<10?'0'+selectedVal[2]:selectedVal[2]} ${selectedVal[3]<10?'0'+selectedVal[3]:selectedVal[3]}:00`;
            this.getKeyWordsQuery();
        },
        cancelHandle() {
            this.$createToast({
                type: 'correct',
                txt: 'Picker canceled',
                time: 1000
            }).show()
        },
        handleArticle(){
            this.handleSelectClass[0].class='selectStatusT'
            this.handleSelectClass[1].class='selectStatusF'
        },
        handleChartMap(){
            this.handleSelectClass[0].class='selectStatusF'
            this.handleSelectClass[1].class='selectStatusT'
        }
    },
    watch:{
        imageType(n,o){
            this.handleImage1=before1;
            this.handleImage2=before2;
            this.handleImage3=before3;
            this.handleImage4=before4;
            n.map(i=>{
                if(i==1){
                    this[`handleImage${1}`]=active1
                }else if(i==2){
                    this[`handleImage${2}`]=active2
                }else if(i==3){
                    this[`handleImage${3}`]=active3
                }else if(i==4){
                    this[`handleImage${4}`]=active4
                }
                
            })
        }
    }
}
</script>
<style scoped>

.handleSelect{
    height:0.8rem;
    padding:0.1rem;
}
.handleHeadline{
    width:20%;
    height:0.8rem;
    float: left;
   
}
.headlineBG{
    width: 100%;
    height:100%;
    /* background: #D2D2D2;
    border-radius: 0.5rem;
    line-height:0.8rem; */
    position:relative;
    bottom:0.05rem;
}
.handleHeadline span{
    margin-top:0;
    font-size: 0.33rem;
    color: #ACACAC;
    padding:0.2rem 0;
    display: inline-block;
    width: 100%;
    background: #d2d2d2; 
    border-radius: 0.5rem;
    position: relative;
    bottom:0.05rem;
  
}
.handleSelectContent{
    width:75%;
    float:right;
    height:40px;
    text-align: left;
    line-height: 0.6rem;
    color:#ACACAC;
}
.handleSelectContent:nth-of-type(1) span{
    font-size:0.37rem;
    display: inline-block;
    margin: 0 0.3rem;
    text-align: center;
}
.handleSelectContent:nth-of-type(2) span{
    font-size:0.37rem;
    display: inline-block;
    margin: 0 0.3rem;
}
.handleSelectContent:nth-of-type(3) span{
    font-size:0.37rem;
    display: inline-block;
    margin: 0 0.3rem;
}
.hanleSelectDate span{
    font-size:0.37rem;
    display: inline-block;
    margin: 0 0.1rem;
    text-align: center;
}
.DataShowSelect span{
    margin-right:0.5rem;
}
.selectStatusT{
    color:#ED4C4C;
    text-decoration: underline;
}
</style>



